* {
  box-sizing: border-box;
}

html {
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.body {
  width: 100%;
  height: 100%;
  flex-wrap: wrap;
  margin: 0;
  padding: 20px;
  display: flex;
  position: relative;
}

.body.body--colorswitcher {
  justify-content: center;
  align-items: center;
  transition: background-color .25s;
}

.body.body--colorswitcher p {
  display: inline-block;
  position: absolute;
  top: 0;
  left: 20px;
}

.body.body--colorswitcher p a {
  color: #212121;
  border-bottom: 1px solid #212121;
  text-decoration: none;
}

.body.body--colorswitcher button {
  cursor: pointer;
  opacity: 1;
  text-transform: uppercase;
  background: #eee;
  border: 1px solid #ccc;
  margin: 5px;
  padding: 10px 30px;
  font-size: 16px;
  line-height: 1.1;
  transition: opacity .25s;
}

.body.body--colorswitcher button:disabled {
  opacity: .5;
  cursor: default;
}

